<script>
import PullTo from '@/components/vue-pull-to/vue-pull-to.vue'

export default {
  components: {
    PullTo
  },
  data() {
    return {
      titleName: '',
      firPdate: '20190127',
      firPname: 'MEDY',
      firPcontent: '',
      secPone: '',
      secPtwo: '()',
      secPthree: '2,0004,000',
      secPfour: ' MEDY12,0004,000 ',
