      secPsix: '4,000/1 ex)520,000',
      secPseven: '',
      secPseven2: '',
      secPeight: '',
    }
  },
  mounted: function(){
    this.init();
  },
  methods: {
    init: function(){
      this.$refs.noticePageCol.style.height = window.innerHeight - 42 + 'px';
    },
    test1: function () {
      this.$router.push({ path: '/notice' });
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    refresh: function(loaded){
      setTimeout(function(){
        loaded('done');
      },1000);
    }
  }
}
</script>

