        c = document.getElementById("keyword").scrollHeight;
      }
      //var d= document.getElementById("putDown").scrollHeight;
      var needRefreshHeight = height - pageHeight - loadingHeight + (298 + c - 42);
      var _this = this;
      if ((scrollTop >= needRefreshHeight) && (this.keywordButtonClicked)) {
